import React from "react";
import { useState } from "react";
import { useWallet, UseWalletProvider } from 'use-wallet'; 
import '../style.css';
import 'semantic-ui-css/semantic.min.css';
import { Button, Modal, Grid } from 'semantic-ui-react';
import { Checkbox } from 'semantic-ui-react'
// connect with web3
import {OracleOpen_contract, TokenList_contract} from '../web3.js';
//import logo
import logo from '../assets/logo.png';

const markets = ["ONE", "USDT", "WBTC"];

let marketsAddress;

async function getMarketsAddress() {
    marketsAddress= [
        await TokenList_contract.methods.getMarketAddress("0xABCD7374737972696e6700000000000000000000000000000000000000000000").call(),
        await TokenList_contract.methods.getMarketAddress("0xABCD7374737972696e6700000000000000000000000000000000000000000001").call(),
        await TokenList_contract.methods.getMarketAddress("0xABCD7374737972696e6700000000000000000000000000000000000000000002").call()
    ];
    console.log("Address:  ", marketsAddress);
}
getMarketsAddress();

const depositMarketsData = [
    {assetId: 0, assetName: markets[0], AssetFullname: "One Token", APY: 0.10, Collateral: <Checkbox slider />, amount: 0},
    {assetId: 1, assetName: markets[1], AssetFullname: "Tether", APY: 0.40, Collateral: <Checkbox slider />, amount: 0},
    {assetId: 2, assetName: markets[2], AssetFullname: "Wrapped BTC", APY: 0.08, Collateral: <Checkbox slider />, amount: 0},
];

const borrowMarketsData = [
    {assetId: 0, assetName: markets[0], AssetFullname: "One Token", APY: 3.38, Liquidity: 4830000},
    {assetId: 1, assetName: markets[1], AssetFullname: "Tether", APY: 5.64, Liquidity: 112210000},
    {assetId: 2, assetName: markets[2], AssetFullname: "Wrapped BTC", APY: 2.72, Liquidity: 7064090000},
];

let latestPrice;
async function getPrice() {
    latestPrice = [
        await OracleOpen_contract.methods.getLatestPrice("0xcEe686F89bc0dABAd95AEAAC980aE1d97A075FAD").call(),
        await OracleOpen_contract.methods.getLatestPrice("0x9A37E1abFC430B9f5E204CA9294809c1AF37F697").call(),
        await OracleOpen_contract.methods.getLatestPrice("0xEF637736B220a58C661bfF4b71e03ca898DCC0Bd").call()
    ];
    for(var i = 0; i < 3; i++) {
        latestPrice[i] = parseFloat(latestPrice[i]) / (10 ** 8);
    }
    console.log(latestPrice);
}
getPrice();

let depositTemp = 0, borrowTemp = 0;
//Dashboard component
const Dashboard = () => {
    
    let temp = 0;
    const [depositAmount, setDeposit] = useState();
    const [borrowBalance, setBorrow] = useState();
    const [isConnected, setConnection] = useState(false);

    function DepositWithdraw(props) {
        const [open, setOpen] = useState(false)
        const [method, setMethod] = useState("Deposit");
        
        const handleDeposit = () => {
            depositTemp += Number(temp) * latestPrice[props.assetID];
            borrowTemp += 3 * Number(temp) * latestPrice[props.assetID];
            setDeposit(depositTemp);
            setBorrow(borrowTemp);
        }

        const handleWithdraw = () => {
            depositTemp -= Number(temp) * latestPrice[props.assetID];
            borrowTemp -= 3 * Number(temp) * latestPrice[props.assetID];
            setDeposit(depositTemp);
            setBorrow(borrowTemp);
        }

        return (
            <Modal
                onClose={() => setOpen(false)}
                onOpen={() => setOpen(true)}
                open={open}
                trigger={<Button>Deposit</Button>}
                style={{textAlign: "center", width: '480px'}}
            >
                { isConnected ?
                <>
                <Modal.Header>{markets[props.assetID]}</Modal.Header>
                { method === "Deposit" ? 
                    <div>
                        <Modal.Actions>
                            <Grid divided="vertically">
                                <Grid.Row>
                                    <Button className="col-sm-6">Deposit</Button>
                                    <Button className="col-sm-6" onClick={() => {setMethod("Withdraw")}}>Withdraw</Button>
                                </Grid.Row>
                                <Grid.Row>
                                    <Grid.Column>   
                                        <h2>Deposit Amount: </h2>
                                        <form onSubmit={handleDeposit}>
                                            <input type="text" placeholder="Enter the amount" style={{height: '30px'}}
                                            onChange={(event) => {temp = event.target.value}}></input>
                                            <Button>Deposit</Button>
                                        </form>
                                    </Grid.Column>
                                </Grid.Row>
                            </Grid>
                        </Modal.Actions>
                    </div>
                    :
                    <div>
                        <Modal.Actions>
                            <Grid divided="vertically">
                                <Grid.Row>
                                    <Button className="col-sm-6" onClick={() => {setMethod("Deposit")}}>Deposit</Button>
                                    <Button className="col-sm-6">Withdraw</Button>
                                </Grid.Row>
                                <Grid.Row>
                                    <Grid.Column>   
                                        <h2>Withdraw Amount: </h2>
                                        <form onSubmit={handleWithdraw}>
                                            <input type="text" placeholder="Enter the amount" style={{height: '30px'}}
                                            onChange={(event) => {temp = event.target.value}}></input>
                                            <Button>Withdraw</Button>
                                        </form>
                                    </Grid.Column>
                                </Grid.Row>
                            </Grid>
                        </Modal.Actions>
                    </div>
                }
                </>
                : <h2>You are not connected to your wallet.</h2>
                }
            </Modal>
        )
    }

    function BorrowRepay(props) {
        const [open, setOpen] = useState(false)
        const [method, setMethod] = useState("Borrow");
        
        const handleBorrow = () => {
            borrowTemp -= Number(temp) * latestPrice[props.assetID];
            setBorrow(borrowTemp);
        }

        const handleRepay = () => {
            borrowTemp += Number(temp) * latestPrice[props.assetID];
            setBorrow(borrowTemp);
        }

        return (
            <Modal
                onClose={() => setOpen(false)}
                onOpen={() => setOpen(true)}
                open={open}
                trigger={<Button>Borrow</Button>}
                style={{textAlign: "center", width: '480px'}}
            >
            { isConnected ?
            <>
            <Modal.Header>{markets[props.assetID]}</Modal.Header>
            { method === "Borrow" ? 
                <div>
                    <Modal.Actions>
                        <Grid divided="vertically">
                            <Grid.Row>
                                <Button className="col-sm-6">Borrow</Button>
                                <Button className="col-sm-6" onClick={() => {setMethod("Repay")}}>Repay</Button>
                            </Grid.Row>
                            <Grid.Row>
                                <Grid.Column>   
                                    <h2>Borrow Amount: </h2>
                                    <form onSubmit={handleBorrow}>
                                        <input type="text" placeholder="Enter the amount" style={{height: '30px'}}
                                        onChange={(event) => {temp = event.target.value}}></input>
                                        <Button>Borrow</Button>
                                    </form>
                                </Grid.Column>
                            </Grid.Row>
                        </Grid>
                    </Modal.Actions>
                </div>
                :
                <div>
                    <Modal.Actions>
                        <Grid divided="vertically">
                            <Grid.Row>
                                <Button className="col-sm-6" onClick={() => {setMethod("Borrow")}}>Borrow</Button>
                                <Button className="col-sm-6">Repay</Button>
                            </Grid.Row>
                            <Grid.Row>
                                <Grid.Column>   
                                    <h2>Repay Amount: </h2>
                                    <form onSubmit={handleRepay}>
                                        <input type="text" placeholder="Enter the amount" style={{height: '30px'}}
                                        onChange={(event) => {temp = event.target.value}}></input>
                                        <Button>Repay</Button>
                                    </form>
                                </Grid.Column>
                            </Grid.Row>
                        </Grid>
                    </Modal.Actions>
                </div>
                }
                </>
                : <h2>You are not connected to your wallet.</h2>}
            </Modal>
        )
    }

    function DepositTable() {
        return (
            <table>
                <thead style={{fontSize: '18px', fontFamily: 'sans-serif'}}>
                    <th>Asset</th>
                    <th>APY</th>
                    <th>Wallet</th>
                    <th></th>
                    <th>Collateral</th>
                </thead>
                <tbody style={{fontSize: '14px', fontFamily: 'sans-serif'}}>
                    {depositMarketsData.map(token => {
                        return (
                            <tr key={token.assetName}>
                                <td style={{padding: '30px', width: '20%'}}>{token.assetName}</td>
                                <td style={{padding: '30px', width: '20%'}}>{token.APY}%</td>
                                <td style={{padding: '30px', width: '20%'}}>{token.AssetFullname}</td>
                                <td style={{padding: '30px', width: '20%'}}><DepositWithdraw assetID={token.assetId}/></td>
                                <td style={{padding: '30px', width: '20%'}}>{token.Collateral}</td>   
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        )
    }

    function BorrowTable() {
        return (
            <table>
                <thead style={{fontSize: '18px', fontFamily: 'sans-serif'}}>
                    <th>Asset</th>
                    <th>APY</th>
                    <th>Wallet</th>
                    <th></th>
                    <th>Liquidity</th>
                </thead>
                <tbody style={{fontSize: '14px', fontFamily: 'sans-serif'}}>
                    {borrowMarketsData.map(token => {
                        return (
                            <tr key={token.assetName} >
                                <td style={{padding: '30px', width: '20%' }}>{token.assetName}</td>
                                <td style={{padding: '30px', width: '20%' }}>{token.APY}%</td>
                                <td style={{padding: '30px', width: '20%' }}>{token.AssetFullname}</td>
                                <td style={{padding: '30px', width: '20%' }}><BorrowRepay assetID={token.assetId}/></td>
                                <td style={{padding: '30px', width: '20%' }}>${token.Liquidity>1000000 ? token.Liquidity/1000000+"M":token.Liquidity}</td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        )
    }

    //Connect wallet
    function App() {
        const wallet = useWallet()
        
        const handleDisconnect = () => {
            wallet.reset();
            setConnection(false);
        }
        const handleConnect = () => {
            wallet.connect();
            setConnection(true);
        }
        return (
        <>
            {wallet.status === 'connected' ? (
            <div> 
                <button onClick={ handleDisconnect }>Disconnect </button>
            </div>
            ) : (
            <div>
                <button onClick={ handleConnect }>Connect</button>
            </div>
            )}
        </>
        )
    }

    return (
        
        <div className="main">
            {/* Header */}
            <div className="header">
                <div className="col-xs-3"> 
                    <img src={logo} className="logo" /> 
                    &nbsp;Hashstack
                </div>
                <div className="col-xs-6 text-center links">Dashbodard</div>
                <div className="col-xs-9 text-right actions">
                    <UseWalletProvider
                        chainId={1}
                        connectors={{
                        // This is how connectors get configured
                            injected: {},
                        }}
                    >
                        <App />
                    </UseWalletProvider>
                </div>
            </div>
            <div>
                <div className="balance-totals">
                    <div className="content">
                        <div className="row align-middle">
                            {/* Deposit Balance */}
                            <div className="col-xs-5 text-center">
                                <label className="deposit">Deposit Balance</label>
                                { depositAmount? <div className="headline"><label>${depositAmount}</label></div>
                                    :<div className="headline headline--loading"></div>}
                            </div>
                            {/* Net APY */}
                            <div className="col-xs-2">
                                <div className="net-apy-wrapper">
                                    <div className="net-apy">
                                        <svg viewBox="0 0 140 140" width="100%">
                                            <path d="M 70 70 L  70 0 A 70 70 0 0 1 70 0 Z" stroke="transparent" fill="#9669ED"></path><path d="M 70 70 L  70 0 A 70 70 0 1 1 69.99956017702848 1.381744718642608e-9 Z" stroke="transparent" fill="#00D395"></path>
                                        </svg>
                                    </div>
                                    <div className="net-apy-description">
                                        <label>Net APY</label>
                                        <div className="headline headline--loading"></div>
                                    </div>
                                </div>
                            </div>
                            {/* Borrow Balance */}
                            <div className="col-xs-5 text-center">
                                <label className="borrow">Borrow Balance</label>
                                { borrowBalance? <div className="headline"><label>${borrowBalance}</label></div>
                                    :<div className="headline headline--loading"></div>}
                            </div>
                        </div>
                        
                    </div>
                </div>
                <div className="container-large">
                    <div id="interface-container" className="row">
                        {/* Depositing */}
                        <div id="collateral-pane" className="col-sm-6">
                            <div className="panel">
                                <div className="panel__header align-between"><h4>Deposit Markets</h4></div>
                                {/* Deposit asset list */}
                                <div className="asset-list">
                                    <DepositTable />
                                </div>
                            </div>                            
                        </div>
                        <div style={{border:'1px solid'}}></div>
                        {/* Borrwing */}
                        <div id="borrowing-pane" className="col-sm-6">
                            <div className="panel">
                                <div className="panel__header align-between"><h4>Borrow Markets</h4></div>
                                {/* Borrow asset list */}
                                <div className="asset-list">
                                    <BorrowTable />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    ); 

}
export default Dashboard;
